*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-size: 1rem;
  color: #444444;
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #444444;
}

.logo {
  line-height: 1px;
}
@media (max-width: 600px) {
  .logo {
    margin-top: 20px;
  }
}
.logo a {
  display: block;
}

.wrapper {
  max-width: 960px;
  padding: 0 4%;
  margin: 0 auto 130px auto;
  font-size: 0.9rem;
}
@media (max-width: 600px) {
  .wrapper {
    margin-bottom: 70px;
  }
}

.sec-title {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 60px;
}
@media (max-width: 600px) {
  .sec-title {
    margin-bottom: 40px;
  }
}

/*-------------------------------------------
header
-------------------------------------------*/
#header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 960px;
  height: 270px;
  margin: 0 auto;
  padding: 0 4%;
}
@media (max-width: 600px) {
  #header {
    flex-direction: column;
    height: auto;
    max-width: 100%;
  }
}
#header .navigation {
  display: flex;
  padding: 10px 0;
}
#header .navigation li {
  margin-left: 30px;
}
@media (max-width: 600px) {
  #header .navigation li {
    font-size: 0.8rem;
    margin-left: 20px;
  }
}
@media (max-width: 600px) {
  #header .navigation li:first-child {
    margin-left: 0;
  }
}
#header .navigation li:hover {
  opacity: 0.8;
}
#header .navigation .icon {
  width: 20px;
}

/*-------------------------------------------
mainvisual
-------------------------------------------*/
#mainvisual {
  margin-bottom: 80px;
}
#mainvisual img {
  width: 100%;
  max-width: 1920px;
  height: 420px;
  object-fit: cover;
}

/*-------------------------------------------
about
-------------------------------------------*/
#about ul {
  margin-bottom: 30px;
}
#about ul li:first-child {
  margin-bottom: 30px;
}
#about p {
  text-align: justify;
}

/*-------------------------------------------
works
-------------------------------------------*/
#works ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 50px;
}
@media (max-width: 600px) {
  #works ul {
    flex-direction: column;
  }
}
#works ul li {
  width: 31%;
  margin-bottom: 23px;
}
@media (max-width: 600px) {
  #works ul li {
    width: 100%;
  }
}

/*-------------------------------------------
news
-------------------------------------------*/
#news dl {
  display: flex;
  flex-wrap: wrap;
  border-top: 1px solid #444444;
  margin-bottom: 50px;
}
@media (max-width: 600px) {
  #news dl {
    flex-direction: column;
  }
}
#news dl dt {
  width: 20%;
  border-bottom: 1px solid #444444;
  padding: 15px;
}
@media (max-width: 600px) {
  #news dl dt {
    width: 100%;
    border-bottom: none;
    padding-bottom: 0;
  }
}
#news dl dd {
  width: 80%;
  border-bottom: 1px solid #444444;
  padding: 15px;
}
@media (max-width: 600px) {
  #news dl dd {
    width: 100%;
    padding-top: 0;
  }
}

/*-------------------------------------------
contact
-------------------------------------------*/
#contact dl {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
@media (max-width: 600px) {
  #contact dl {
    flex-direction: column;
  }
}
#contact dl dt {
  width: 15%;
}
@media (max-width: 600px) {
  #contact dl dt {
    width: 100%;
  }
}
#contact dl dd {
  width: 85%;
  margin-bottom: 10px;
}
@media (max-width: 600px) {
  #contact dl dd {
    width: 100%;
  }
}
#contact dl dd input,
#contact dl dd textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #444444;
}
#contact dl dd textarea {
  height: 10rem;
}
#contact .button {
  text-align: center;
}
#contact .button input {
  width: 200px;
  background: black;
  border: none;
  color: #fff;
  padding: 15px 0;
  border: 1px solid #444444;
  font-size: 1rem;
  cursor: pointer;
}
#contact .button input:hover {
  background: #fff;
  color: black;
}

/*-------------------------------------------
footer
-------------------------------------------*/
#footer {
  font-size: 0.5rem;
  text-align: center;
  color: #fff;
  background: black;
  padding: 10px 20px;
}